import React, {useState,useEffect} from 'react'
import { Link } from 'react-router-dom'
import {Form,Button,Row,Col} from 'react-bootstrap'
import { useDispatch,useSelector } from 'react-redux'
import Loader from '../components/Loader'
import Message from '../components/Message'
import { register } from '../actions/userAction'
import FormContainer from '../components/FormContainer'

function RegisterScreen({location,history}) {
    const [name,setName] = useState('')
    const [email,setEmail] = useState('')
    const [password,setPassword] = useState('')
    const [confirmpassword,setConfirmPassword] = useState('')
    const [message,setMessage] = useState('')
    const redirect = location.search ? location.search.split('=')[1] : '/'
    const dispatch = useDispatch()
    const userRegister = useSelector(state => state.userRegister)
    const {loading,error,userInfo} = userRegister
    useEffect(() => {
      if (userInfo){
        history.push(redirect)
      }
    },[history,userInfo,redirect])
    const submitHandler = (e) => {
      e.preventDefault()
      if (password !== confirmpassword){
        setMessage('Passwords Do Not Match')
    }else{
        dispatch(register(name,email, password))
    }
  }
    return (
      <FormContainer>
        <h1>Sign In</h1>
        {message && <Message variant = 'danger'>{message}</Message>}
        {error && <Message variant = 'danger'>{error}</Message>}
        {loading && <Loader/> }
        <Form onSubmit = {submitHandler}>
          <Form.Group controlId = "name">
            <Form.Label>UserName</Form.Label>
              <Form.Control required type = "username" placeholder = "Enter Name" value = {name}
                onChange = {(e) => setName(e.target.value)}>
                </Form.Control>
              </Form.Group>
          <Form.Group controlId = "email">
            <Form.Label>Email Address</Form.Label>
              <Form.Control required type = "email" placeholder = "Enter Email" value = {email}
                onChange = {(e) => setEmail(e.target.value)}>
              </Form.Control>
          </Form.Group>
          <Form.Group controlId = "password">
            <Form.Label>Password</Form.Label>
              <Form.Control required type = "password" placeholder = "Enter Password" value = {password}
                onChange = {(e) => setPassword(e.target.value)}>
              </Form.Control>
          </Form.Group>
          <Form.Group controlId = "confirmpassword">
            <Form.Label>Confirm Password</Form.Label>
              <Form.Control required type = "password" placeholder = "Enter Password Again" value = {confirmpassword}
                onChange = {(e) => setConfirmPassword(e.target.value)}>
              </Form.Control>
          </Form.Group>
          <Button type = 'submit' variant = "primary" className = 'my-3'>Register</Button>
        </Form>
        <Row className = 'py-3'>
          <Col>
            Having Account?<Link
              to = {redirect ? `/login?redirect=${redirect}` : '/register'}>
              Sign In
              </Link>
          </Col>
        </Row>
      </FormContainer>

    )
}

export default RegisterScreen
