import React, {useState,useEffect} from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import {Table,Button} from 'react-bootstrap'
import { useDispatch,useSelector } from 'react-redux'
import Loader from '../components/Loader'
import Message from '../components/Message'
import { listusers,deleteuser } from '../actions/userAction'
function UserListScreen({history}) {
  const dispatch = useDispatch()
  const userList = useSelector(state => state.userList)
  const userLogin = useSelector(state => state.userLogin)
  const {userInfo} = userLogin
  const {loading,error,users} = userList
  const userDelete = useSelector(state => state.userDelete)
  const {success: successDelete} = userDelete

  useEffect(() => {
    if (userInfo && userInfo.isAdmin){
    dispatch(listusers())
  }else{
    history.push('/login')
  }
},[dispatch,history,successDelete,userInfo])
const deleteHandler = (id) => {
  if(window.confirm("Are you sure  you want to delete this user?")){
    dispatch(deleteuser(id))
  }
}
    return (
        <div>
          <h1>Users</h1>
          {successDelete && <Message variant = "success">{successDelete}</Message>}
          {loading ?(
           <Loader/>)
           : error ? (<Message variant = 'danger'>"User Deletio Successful"</Message>)
           :(
             <Table striped bordered hover responsive className='table-sm'>
                            <thead>
                                <tr>
                                    <th>ID</th>
                                    <th>NAME</th>
                                    <th>EMAIL</th>
                                    <th>ADMIN</th>
                                    <th></th>
                                </tr>
                            </thead>

                            <tbody>
                                {users.map(user => (
                                    <tr key={user._id}>
                                        <td>{user._id}</td>
                                        <td>{user.name}</td>
                                        <td>{user.email}</td>
                                        <td>{user.isAdmin ? (
                                            <i className='fas fa-check' style={{ color: 'green' }}></i>
                                        ) : (
                                                <i className='fas fa-check' style={{ color: 'red' }}></i>
                                            )}</td>

                                        <td>
                                            <LinkContainer to={`/admin/user/${user._id}/edit`}>
                                                <Button variant='light' mr = {3} className='btn-sm'>
                                                    <i className='fas fa-edit'></i>
                                                </Button>
                                            </LinkContainer>

                                            <Button variant='danger' id = "trash-button" className='btn-sm' onClick={() => deleteHandler(user._id)}>
                                                <i className='fas fa-trash'></i>
                                            </Button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                    )}
        </div>
    )
}

export default UserListScreen
