import React, {useEffect}from 'react'
import {Link} from 'react-router-dom'
import {useDispatch,useSelector} from 'react-redux'
import {Row,Col,ListGroup,Image,Form,Button,Card} from 'react-bootstrap'
import Message from '../components/Message'
import {addToCart, removeFromCart} from '../actions/cartActions'


function CartScreen({match,location,history}){
    const productId = match.params.id
    const qty = location.search ? Number(location.search.split('=')[1]) : 1
    const dispatch = useDispatch()
    const cart = useSelector(state => state.cart)
    const {cartItems} = cart

    useEffect(() => {
      if(productId){
        dispatch(addToCart(productId,qty))
      }
    },[dispatch,productId,qty])
    const removeFromCartHandler = (id) => {
      dispatch(removeFromCart(id))
    }
    return (
        <Row>
          <Col md = {8}>
           <h1>Shopping Cart</h1>
           {cartItems.length === 0 ?
             <Message variant = 'info'>
                Your Cart is empty <Link to ='/'>Go Back</Link>
             </Message>
           :
           <ListGroup variant = 'flush'>
              {cartItems.map(item => (
                <ListGroup.Item key = {item.product}>
                  <Row>
                    <Col md = {2} xs = 'auto'>
                      <Image src = {item.image} alt = {item.name} fluid rounded/>
                    </Col>
                    <Col md = {3} xs = 'auto' >
                      <Link className = 'text-decoration-none'  to = {`/product/${item.product}`}>{item.name}</Link>
                    </Col>
                    <Col md = {2} xs = 'auto'>
                      ${item.price}
                    </Col>
                    <Col md = {3} xs = 'auto'>
                      <Form.Control as = 'select' value = {item.qty}
                        onChange = {(e) => dispatch(addToCart(item.product,Number(e.target.value)))}>

                        {
                            [...Array(item.countInStock).keys()].map((x) => (
                              <option key = {x+1} value = {x+1}>
                              {x+1}
                              </option>
                      ))
                    }
                    </Form.Control>
                    </Col>
                    <Col md = {1} >
                      <Button onClick = {() => removeFromCartHandler(item.product)} type = 'button' variant = 'light'>
                        <i className = 'fas fa-trash'></i>
                      </Button>
                    </Col>
                  </Row>
                </ListGroup.Item>
              ))}
           </ListGroup>
         }
          </Col>
          <Col md = {4}>
            <Card>
              <ListGroup variant = 'flush'>
                <ListGroup.Item>
                  <h2>SubTotal ({cartItems.reduce((acc,item) => acc+item.qty,0)}) items</h2>
                  ${cartItems.reduce((acc,item) => acc+item.qty*item.price,0).toFixed(2)}
                </ListGroup.Item>
                <ListGroup.Item>
                  <Button
                    type = 'button' id = 'checkout'className = 'btn-block' disabled = {cartItems.length === 0}> Proceed To Checkout </Button>
                </ListGroup.Item>
              </ListGroup>

            </Card>
          </Col>
        </Row>
    )
}

export default CartScreen
